/*------------------------------------------------------------------
Common featured boxes
-------------------------------------------------------------------*/

.featured-item,
.featured-item.hover-outline h2,
.featured-item.border-box h2,
.featured-item.flat-border-box h2,
.featured-item.flat-border-box .icon,
.featured-item.hover-outline .icon,
.featured-item.hover-outline .icon .colored,
.featured-item.hover-outline .desc {
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -ms-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.featured-item .icon {
  font-size: 30px; /*global font size*/
}

.featured-item .icon .material-icons {
  font-size: 40px;
  line-height: 40px;
}

.featured-item .desc p {
  margin: 0;
}

.featured-item h2 {
  font-weight: 500;
}

@media (max-width: 991px) {
  .featured-item {
    margin-bottom: 30px;
  }
}

/*------------------------------------------------------------------
    Featured Box
-------------------------------------------------------------------*/

.featured-boxes {
  > div > div > div {
    > div:nth-child(2), > div:nth-child(4), > div:nth-child(6) {
      > div.featured-box {
        background-color: $featured-box-second-bg-color;
      }
    }
    > div:nth-child(1), > div:nth-child(3), > div:nth-child(5) {
      > div.featured-box {
        background-color: $featured-box-first-bg-color;
      }
    }
  }
  .featured-box {
    position: relative;
    overflow: hidden;
    min-height: 300px;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    transition: all 0.3s;
    margin-bottom: 20px;
    @include border-radius(5px);
    &:hover {
      .overlay-image {
        opacity:$featured-box-overlay-hover-opacity;
        @include blur-effect(2);
      }
    }
    .overlay-image {
      @include border-radius(5px);
      width: 100%;
      height: 300px;
      background-position: center center;
      background-repeat: no-repeat;
      background-size: cover;
      transition: all 0.3s;
      opacity: $featured-box-overlay-opacity;
      @include blur-effect(0.65);
    }
  }
}

.featured-wrapper .intro-header {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;

  -webkit-transition: -webkit-transform 0.35s;
  -moz-transition: -webkit-transform 0.35s;
  -ms-transition: -webkit-transform 0.35s;
  transition: transform 0.35s;

  -webkit-transform: translate3d(0, -50%, 0);
  -moz-transform: translate3d(0, -50%, 0);
  -ms-transform: translate3d(0, -50%, 0);
  transform: translate3d(0, -50%, 0);
}

.featured-box:hover .featured-wrapper .intro-header {
  -webkit-transform: translate3d(0, -50%, 0) translate3d(0, -60px, 0);
  -moz-transform: translate3d(0, -50%, 0) translate3d(0, -60px, 0);
  -ms-transform: translate3d(0, -50%, 0) translate2d(0, -60px, 0);
  transform: translate3d(0, -50%, 0) translate3d(0, -60px, 0);
}

.featured-wrapper .intro-header i {
  font-size: 34px;
  margin-bottom: 25px;
}

.featured-wrapper .intro-header i[class^="flaticon-"] {
  margin-bottom: 10px;
  line-height: 55px;
}

.featured-wrapper .intro-header i[class^="flaticon-"]:before {
  font-size: 55px;
}

.featured-wrapper .intro-header i.material-icons {
  font-size: 40px;
  color: $featured-box-icon-color;
}

.featured-wrapper .intro-header h2 {
  font-weight: 500;
  a {
    color: $featured-box-link-color;
    &:hover {
      color: $featured-box-link-hover;
    }
  }
}

.featured-box .content {
  color: $featured-box-txt-color;
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 20px;
  width: 100%;
  opacity: 0;

  -webkit-transform: translate3d(0, 10px, 0);
  -moz-transform: translate3d(0, 10px, 0);
  -ms-transform: translate3d(0, 10px, 0);
  transform: translate3d(0, 10px, 0);

  -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
  -moz-transition: opacity 0.35s, -webkit-transform 0.35s;
  -ms-transition: opacity 0.35s, -webkit-transform 0.35s;
  transition: opacity 0.35s, transform 0.35s;
}

.featured-box:hover .content {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.featured-box:hover {
  box-shadow: 0 12px 15px 0 rgba(0, 0, 0, 0.24), 0 17px 50px 0 rgba(0, 0, 0, 0.19);
}

/*IE9 Fixing*/
.ie9 .featured-box .featured-wrapper .intro-header {
  top: 20%;
}

/*colored icon*/
.brand-icon {
  color: #03a9f4;
}

.pink-icon {
  color: #ed145b;
}

.purple-icon {
  color: #9c27b0;
}

.deep-purple-icon {
  color: #673ab7;
}

.green-icon {
  color: #8bc34a;
}

.cyan-icon {
  color: #00bcd4;
}

.red-icon {
  color: #f6483f;
}

.yellow-icon {
  color: #f5c400;
}

.dark-icon {
  color: #202020;
}

.brand-hover:hover {
  background-color: #03a9f4 !important;
}

.pink-hover:hover {
  background-color: #ed145b !important;
}

.purple-hover:hover {
  background-color: #9c27b0 !important;
}

.deep-purple-hover:hover {
  background-color: #673ab7 !important;
}

.green-hover:hover {
  background-color: #8bc34a !important;
}

.hover:hover i,
.hover:hover h2,
