/* ----------------------------------------------------------------
Hover outline
-----------------------------------------------------------------*/
.featured-item.hover-outline {
  padding: 30px 20px;
}

.featured-item.hover-outline .icon {
  float: left;
  margin-right: 20px;
}

.featured-item.hover-outline .desc {
  overflow: hidden;
}

.featured-item.hover-outline:hover h2,
.featured-item.hover-outline:hover .icon,
.featured-item.hover-outline:hover .icon .colored,
.featured-item.hover-outline:hover .icon .brand-icon,
.featured-item.hover-outline:hover .desc {
  color: #fff !important;
}

.featured-item.hover-outline:hover {
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}

/* ----------------------------------------------------------------
Feature icon
-----------------------------------------------------------------*/
.featured-item.feature-icon .icon {
  float: left;
  margin-right: 20px;
  width: 70px;
  height: 70px;
  font-size: 25px;
  line-height: 68px;
  text-align: center;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -ms-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.featured-item.feature-icon .desc {
  overflow: hidden;
}

/* ----------------------------------------------------------------
Feature icon Border Right
-----------------------------------------------------------------*/
.featured-item.feature-icon.border-right {
  border-right: 1px solid #eee;
  padding: 40px 25px;
}

.featured-item.feature-icon.border-right.last {
  border-right: 1px solid transparent;
}

/* ----------------------------------------------------------------
Feature Background
-----------------------------------------------------------------*/

.feature-background {
  background-image: url(../../img/page-header/header-2.jpg);
  background-size: cover;
  background-attachment: fixed;
  background-repeat: no-repeat;
  position: relative;
}

.feature-background::before {
  content: "";
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}

.feature-background .featured-item.feature-icon {
  margin-top: 0;
}

.fullwidth .featured-item.feature-icon .icon {
  border: 0;
  background-color: transparent;
  color: #fff;
  font-size: 40px;
}

.feature-background .featured-item.pink-hover:hover {
  background-color: #ed145b;
}

.feature-background .featured-item.purple-hover:hover {
  background-color: #673ab7;
}

.feature-background .featured-item.blue-hover:hover {
  background-color: #03a9f4;
}

/* ----------------------------------------------------------------
Border Box Style
-----------------------------------------------------------------*/

.featured-item.border-box {
  padding: 50px 20px;
  text-align: center;
  border: 1px solid #eee;
  background-color: #fff;
}

.no-gutter .featured-item.border-box {
  margin-left: -1px;
}

.featured-item.border-box h2 {
  margin-bottom: 20px;
}

.featured-item.border-box:hover h2,
.featured-item.border-box:hover .icon {
  color: #fff;
}

.featured-item.border-box:hover {
  color: #fff;
  border: 1px solid transparent;
  box-shadow: 0 8px 17px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

/*
Owl Carousel Customize
----------------------------------------------------------------*/

.featured-carousel .owl-item {
  padding: 20px 8px;
}

.owl-theme .owl-controls {
  margin: 10px 0;
}

.owl-theme .owl-dots .owl-dot span {
  width: 13px;
  height: 13px;
  background: #dadada;
  -webkit-transition: all 0.3s ease;
  -ms-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

/*colored dot*/
.brand-dot.owl-theme .owl-dots .owl-dot.active span,
.brand-dot.owl-theme .owl-dots .owl-dot:hover span {
  background: #03a9f4;
}

/* ----------------------------------------------------------------
Flat Border Box Style
-----------------------------------------------------------------*/
.featured-item.flat-border-box {
  padding: 40px 35px;
  border: 1px solid #e2e2e2;
}

.featured-item.flat-border-box .icon {
  color: #202020;
  margin-bottom: 30px;
}

.featured-item.flat-border-box h2 {
  margin-bottom: 25px;
}

.featured-item.flat-border-box .desc p {
  margin-bottom: 30px;
}

.featured-item.flat-border-box .readmore {
  color: #999;
  font-weight: 700;
}

.featured-item.flat-border-box .readmore:hover {
  color: #03a9f4;
}

.featured-item.flat-border-box .readmore i {
  padding: 0;
  opacity: 0;
  visibility: hidden;
  -webkit-transition: padding .3s;
  -moz-transition: padding .3s;
  transition: padding .3s;
}

.featured-item.flat-border-box .readmore:hover i {
  opacity: 1;
  visibility: visible;
  padding-left: 10px;
}

.featured-item.flat-border-box:hover h2,
.featured-item.flat-border-box:hover .icon {
  color: #673ab7;
}

.featured-item.flat-border-box:hover {
  box-shadow: 0 8px 17px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

@media (max-width: 991px) {
  .featured-item.flat-border-box {
    margin-bottom: 30px;
  }
}

.featured-item img {
  width: inherit !important;
  margin: 0 auto;
}

/* ----------------------------------------------------------------
Featured Plus Box Style
-----------------------------------------------------------------*/
.featured {
  overflow: hidden;
}

.featured li {
  float: left;
  position: relative;
  padding: 65px 35px 60px;
  text-align: center;
}

.featured.plus-box .icon {
  width: 70px;
  height: 70px;
  line-height: 70px;
  margin: 0 auto 20px;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

.featured.plus-box .icon i.material-icons {
  line-height: 70px;
}

.featured.plus-box .desc h2 {
  margin-bottom: 20px;
}

.featured.plus-box .featured-item:hover .icon {
  color: #03a9f4;
}

/*Grid Style*/
.featured.grid-2 li {
  width: 50%;
}

.featured.grid-3 li {
  width: 33.33%;
}

.featured.grid-4 li {
  width: 25%;
}

.featured.grid-5 li {
  width: 20%;
}

.featured.grid-6 li {
  width: 16.66%;
}

@media screen and (max-width: 991px) {

  .featured.grid-3 li,
  .featured.grid-4 li,
  .featured.grid-5 li,
  .featured.grid-6 li {
    width: 33.33%;
  }
}

@media screen and (max-width: 767px) {
  .featured.grid-2 li,
  .featured.grid-3 li,
  .featured.grid-4 li,
  .featured.grid-5 li,
  .featured.grid-6 li {
    width: 50%;
  }
}

@media screen and (max-width: 479px) {
  .featured.grid-2 li,
  .featured.grid-3 li,
  .featured.grid-4 li,
  .featured.grid-5 li,
  .featured.grid-6 li {
    width: 100%;
  }
}

.featured.plus-box li:after,
.featured.plus-box li:before {
  content: '';
  position: absolute;
}

.featured.plus-box li:before {
  height: 100%;
  top: 0;
  right: -2px;
  border-right: 1px solid #eee;
}

.featured.plus-box li:after {
  width: 100%;
  height: 0;
  top: auto;
  right: 0;
  bottom: -1px;
  border-bottom: 1px solid #eee;
}

/* ----------------------------------------------------------------
Feature Intro
-----------------------------------------------------------------*/
.featured-intro {
  padding: 70px 30px;
}

.featured-intro .icon {
  color: #6fc673;
  margin-bottom: 30px;
}

.featured-intro h2 {
  font-size: 25px;
  font-weight: 400;
  margin-bottom: 25px;
}

.featured-intro.dark-bg h2,
.featured-intro.dark-bg {
  color: #fff;
}

.featured-intro.light-gray-bg h2,
.featured-intro.light-gray-bg {
  color: #202020;
}

/* ----------------------------------------------------------------
Feature Presention
-----------------------------------------------------------------*/
.feature-presentation .featured-item.feature-icon .icon {
  width: 70px;
  height: 70px;
  font-size: 25px;
  line-height: 68px;
  border: 1px solid #e0e0e0;
}

.feature-presentation .featured-item.feature-icon .icon .material-icons {
  font-size: 35px;
  line-height: 68px;
}

.feature-presentation .featured-item.feature-icon:hover .icon {
  border: 1px solid transparent;
}

.feature-presentation .featured-item.feature-icon .material-icons {
  line-height: 58px;
}

@media (min-width: 768px) {

  .featured-item.feature-icon.right-icon {
    text-align: right;
  }
  .featured-item.feature-icon.right-icon .icon {
    float: right;
    margin-right: 0;
    margin-left: 20px;
  }
}

/* ----------------------------------------------------------------
SEO Featured Service
-----------------------------------------------------------------*/
.seo-service img {
  width: 100% !important;
}

.seo-service .desc {
  padding: 30px 20px;
  background-color: #f5f5f5;
  position: relative;
  overflow: hidden;
}

.seo-service .bg-overlay {
  position: absolute;
  bottom: -100px;
  right: -100px;
  width: 130px;
  height: 130px;
  background-color: #03a9f4;
  z-index: 1;
  border-radius: 100%;
  -webkit-transform: scale(0) translateZ(0);
  -moz-transform: scale(0) translateZ(0);
  -ms-transform: scale(0) translateZ(0);
  -o-transform: scale(0) translateZ(0);
  transform: scale(0) translateZ(0);
  -webkit-transition: all 0.5s cubic-bezier(0.39, 0.575, 0.56499, 1);
  -moz-transition: all 0.5s cubic-bezier(0.39, 0.575, 0.56499, 1);
  -ms-transition: all 0.5s cubic-bezier(0.39, 0.575, 0.56499, 1);
  -o-transition: all 0.5s cubic-bezier(0.39, 0.575, 0.56499, 1);
  transition: all 0.5s cubic-bezier(0.39, 0.575, 0.56499, 1);
}

.seo-service:hover .bg-overlay {
  right: 0;
  bottom: 0;
  -webkit-transform: scale(10) translateZ(0);
  -moz-transform: scale(10) translateZ(0);
  -ms-transform: scale(10) translateZ(0);
  -o-transform: scale(10) translateZ(0);
  transform: scale(10) translateZ(0);
}

/*Internet Explorar Fixing*/
.ie9 .seo-service:hover .bg-overlay {
  width: 100%;
  height: 100%;
  border-radius: 0;
}

.seo-service .learn-more {
  color: #fff;
  font-size: 16px;
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  text-align: center;
  z-index: 10;
  opacity: 0;
  visibility: hidden;
  -webkit-transform: translate(0%, 100px);
  -moz-transform: translate(0%, 100px);
  -ms-transform: translate(0%, 100px);
  -o-transform: translate(0%, 100px);
  transform: translate(0%, 100px);
  -webkit-transition: 0.5s cubic-bezier(0.87, -0.41, 0.19, 1.44);
  -moz-transition: 0.5s cubic-bezier(0.87, -0.41, 0.19, 1.44);
  -ms-transition: 0.5s cubic-bezier(0.87, -0.41, 0.19, 1.44);
  -o-transition: 0.5s cubic-bezier(0.87, -0.41, 0.19, 1.44);
  transition: 0.5s cubic-bezier(0.87, -0.41, 0.19, 1.44);
}

.seo-service:hover .learn-more {
  opacity: 1;
  visibility: visible;
  -webkit-transform: translate(0%, -50%);
  -moz-transform: translate(0%, -50%);
  -ms-transform: translate(0%, -50%);
  -o-transform: translate(0%, -50%);
  transform: translate(0%, -50%);
}
